.button {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: max(1.2vw, 1rem);
  color: #ffffff;
  background: #ff902a;
  border: none;
  border-radius: 1rem;
  flex-grow: 1;
  text-align: center;
  padding: 0.5rem;
  text-decoration: none;
  height: fit-content;
  width: 100%;

  cursor: pointer;
  transition: all 0.2s ease-in-out;
  &:hover {
    background-color: darken($color: #ff902b, $amount: 5%);
  }
  &:active {
    background-color: lighten($color: #ff902b, $amount: 5%);
  }
}
